@import '../../../variables.scss';

.account-sign-up{
    padding: 70px 0;
    width: 100%;
    color: $colorTextDarkGrey;
    display: flex;
    justify-content: center;
    background-image: url(../../../assets/img/bg-product-other.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

    .sign-up{
        background-color: $colorWhite;
        width: 700px;
        height: auto;
        padding: 50px;

        h3{
            text-align: center;
            text-transform: uppercase;
            font-size: 30px;
        }
        p{
            text-align: center;
            margin: 10px 0 30px 0;
        }
        form{
            display: flex;
            flex-direction: column;

            .field{
                margin-bottom: 35px;
                position: relative;

                label{
                    text-transform: uppercase;
                    font-size: 14px;
                }
                input{
                    margin-top: 10px;
                    width: 100%;
                    height: 50px;
                    border: 1px solid #dbdbdb;
                    color: $colorTextDarkGrey;
                    font-family: 'Montserrat', sans-serif;
                    font-size: 18px;
                    padding: 0 12px;
                    &:focus{
                        outline: none;
                    }
                    &::placeholder{
                        color: $colorTextGrey;
                        font-family: 'Montserrat', sans-serif;
                        font-weight: 300;
                        font-size: 18px;
                    }
                }
                #password-signup, #confirm-password-signup{
                    padding-right: 42px;
                }
                .password-toggle{
                    position: absolute;
                    top: 45px;
                    right: 12px;
                    width: 24px;
                    height: 24px;
                    padding: 0;
                    background-color: transparent;
                    border: 0;
                    outline: 0;
                    cursor: pointer;

                    .password-toggle_icon{
                        width: 24px;
                        height: 24px;
                        position: absolute;
                        top: 0;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        background-size: contain;
                        background-position: center;
                        background-repeat: no-repeat;
                        opacity: .5;
                        transition: .3s;
                    }
                    .password-toggle_icon_hide{
                        background-image: url(../../../assets/img/hide-icon.svg);
                    }
                    .password-toggle_icon_show{
                        background-image: url(../../../assets/img/show-icon.svg);
                    }
                }
                .password-toggle_hide{
                    .password-toggle_icon_hide{
                        opacity: .5;
                        transition: .3s;
                        &:hover{
                            opacity: 1;
                        }
                    }
                    .password-toggle_icon_show{
                        opacity: 0;
                        pointer-events: none;
                    }
                }
                .password-toggle_show{
                    .password-toggle_icon_show{
                        opacity: .5;
                        transition: .3s;
                        &:hover{
                            opacity: 1;
                        }
                    }
                    .password-toggle_icon_hide{
                        opacity: 0;
                        pointer-events: none;
                    }
                }
                .error{
                    color: rgb(245 62 62);
                    font-size: 14px;
                }
            }
            .checkbox-div{
                display: flex;
                margin-bottom: 35px;
                position: relative;

                input{
                    display: none;
                }
                label{
                    display: block;
                    padding-left: 25px;
                    font-size: 15px;
                    &::before{
                        display: block;
                        width: 16px;
                        height: 16px;
                        background-color: $colorWhite;
                        border: 1px solid $colorTextDarkGrey;
                        content: "";
                        position: absolute;
                        top: 1px;
                        left: 0;
                    }
                }
                input:checked+label{
                    &::before{
                        background-color: $colorBlack;
                        border: 1px solid $colorBlack;
                    }
                    &::after{
                        display: block;
                        width: 15px;
                        height: 15px;
                        background-image: url(../../../assets/img/tick-white.svg);
                        background-size: contain;
                        background-position: center;
                        background-repeat: no-repeat;
                        content: "";
                        position: absolute;
                        top: 2px;
                        left: 1px;
                    }
                }
            }
            .btn-sign-up{
                margin: 0 auto;
            }
        }
    }
}

@media (max-width: 768px) {
    .account-sign-up{
        .sign-up{
            width: 80%;
        }
    }
}
@media (max-width: 550px) {
    .account-sign-up{
        .sign-up{
            padding: 20px;
            h3{
                font-size: 27px;
            }
            p{
                font-size: 16px;
            }
            form{
                .field{
                    label{
                        font-size: 13px;
                    }
                    input{
                        font-size: 16px;
                        &::placeholder{
                            font-size: 14px;
                        }
                    }
                    .error {
                        font-size: 12px;
                    }
                }
                .checkbox-div{
                    label{
                        font-size: 14px;
                    }
                }
                .btn-sign-up{
                    padding: 0px 15px;
                    font-size: 16px;
                }
            }
        }
    }
}


